export default {
    layout: 'Adm-content',
    data(){
        return{
            activeBatchs:'',

        }
    },
    mounted(){
         this.getbatch();

    },
    methods:{
            getbatch() {
            this.$axios
                .$get("admission/batch-show")
                .then((res) => {
                    console.log(res);
                    this.activeBatchs = res;
                })
                .catch((err) => {
                    console.log(err);
                });
        },
    }
}
